header {
    background-color: $accent-one !important;
    position: relative;
    .burger {
        display: none;
        &:hover {
            svg {
                fill: $accent-two; } } }
    nav {
        background-color: $accent-one;
        transition: 300ms;

        &.hide {
            display: block; }

        ul {
            display: flex;
            li {
                padding: 10px; }
            .navOption {
                text-decoration: none;
                color: $light;
                font-size: 14px;
                cursor: pointer;
                &:hover {
                     color: $accent-two-light; } } } }

    .cover {
        display: none; }

    .navToolBar {
        display: flex;
        justify-content: space-between; } }



@media(max-width: $mobile-break) {
    header {
        .cover {
            display: block;
            background-color: black;
            opacity: 0.2;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            z-index: 9; }
        .burger {
            display: block; }
        nav.hide {
            display: none; }
        nav.showNav {
            display: block !important;
            position: absolute;
            width: 100%;
            top: 50px;
            left: 0px;
            z-index: 10;
            box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12);
            ul {
                display: block; } } } }
