.metaDataList {
    &ItemContainer {
        display: flex;
        justify-content: space-between;
        * {
            display: block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis; } }
    li:nth-child(1) {
        background-color: $accent-one;
        padding-left: 6px;
        * {
            font-size: 16px;
            color: $light; } }
    li {

        padding: 5px 12px;
        * {
            font-size: 14px; } } }
